<script>
import FlatSurfaceShader from 'vue-flat-surface-shader'
import Vue from 'vue'
import JQuery from 'jquery'
let $ = JQuery

export const baseUrl = process.env.PORTFOLIO

import { Navbar } from 'bootstrap-vue/es/components';
import axios from 'axios'

Vue.use(Navbar);
Vue.use(FlatSurfaceShader)

$("body")
  .on('click', '.back_to_top', () => $("html, body").animate({scrollTop: 0}, 500) )
  .on("click",".navbar-toggler, .nav-link", () => {
    if($(".navbar-collapse").hasClass("show")){
      $(".social_links").attr("style","margin-right:0px;");
    }else{
      $(".social_links").attr("style","margin:0px auto;");
    }
  }).on('click',".go_to_contact", () => {
  setTimeout(function(){
    $('html, body').animate({
        scrollTop: $("#contact_us").offset().top
    }, 100);
  },200)
  }).on('click',".button_next", () => {
  setTimeout(() => {
    $('html, body').animate({
        scrollTop: $("#body_cont").offset().top + (-120)
    }, 500);
  },200)
});

$('document').ready(() => {
  var body_width = $("html").width();
  var body_height = $(window).height();

  if(body_width <= 844){
    $(".banner-items").css({"left":10+"px","width":body_width-20+"px", "margin-top":body_height/3+"px"});
    $(".header-banner-cover").css({"margin-top":body_height/25+"px","margin-left":"20px;"});
  }else{
    $(".banner-items").css({"left":body_width/4+"px","width":body_width/2+"px", "margin-top":body_height/4+"px"});
    $(".header-banner-cover").css({"margin-top":body_height/25+"px","margin-left":"-20px;"});
  }

  $(".back_to_top").fadeOut();
  setTimeout(() => {
    $(".page_loader").fadeOut(1000);
  }, 0);

  if($(".navbar-collapse").hasClass("show")){
    $(".social_links").attr("style","margin:0px auto;");
  }else{
    $(".social_links").attr("style","margin-right:0px;");
  }

})


$(window).on("scroll", () => {
    var scrollPos = $(window).scrollTop();
    var scrollPosToOpacity = (100/scrollPos);

    if (scrollPosToOpacity < 0) {
        scrollPosToOpacity = 1;
    }

    if (scrollPos <= 0) {
        $(".back_to_top").fadeOut();
    } else {
        $(".back_to_top").fadeIn();
    }

    if (scrollPos >= 100) {
      $(".navbar").attr("style", "background-color: #212529 !important");
    }else{
      $(".navbar").attr("style", "background-color: none !important");
    }

    $(".header-container").children("div").children("div").css("opacity",scrollPosToOpacity);
});

$("body").on("submit","#form_contact",(e) => {
    e.preventDefault();
    $.ajax({
      url: baseUrl+'/Projects/sendEmail',
      type: 'post',
      dataType: 'application/json',
      data: $("#form_contact").serialize(),
      success: function(data) {
      }
    });

})

$(document).ready(() => {
  var movementStrength = 25;
  var height = movementStrength / $(window).height();
  var width = movementStrength / $(window).width();

  $(".comparison").mousemove((e) => {
    var pageX = e.pageX - ($(window).width() / 2);
    var pageY = e.pageY - ($(window).height() / 2);

    var newvalueX = width * pageX * -1 - 25;
    var newvalueY = height * pageY * -1 - 10;
    $('.banner-items img').css({"left": newvalueX+"px", "margin-top": newvalueY+"px"});
  });

});


export default {
  name: 'App',
  data() {
    return {
      email: 'donbenary.freelance@gmail.com',
      subject: 'Test',
      name: 'Don',
      contact: '091242',
      message: 'Test',
      form_msg: '',
      errors: [],
      success_msg: '',
      email_sent: false,
      titleName: "Don ",
      titleDesignation: "a Coder ",
      txtDisplayed: "",
      step: 0,
      previousStep: 0,
      txtIndex: 0,
      speed: 50
    }
  },
  methods: {
    validateForm(){

      this.email_sent = false

      this.errors = []

      if (!this.email) {
        this.errors.push('Email required.');
      } else if (!this.validEmail(this.email)) {
        this.errors.push('Valid email required.');
      }

      if(!this.contact){
        this.errors.push("Contact number reqired")
      }

      if(!this.name){
        this.errors.push("Name reqired")
      }

      if(!this.subject){
        this.errors.push("Subject reqired")
      }

      if(!this.message){
        this.errors.push("Message reqired")
      }

      if(this.errors.length == 0){
        this.sendEmail()
      }

    },
    validEmail: (email) => {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    sendEmail(){
      $.ajax({
        url: baseUrl+'/Projects/sendEmail',
        type: 'post',
        dataType: 'application/jsonp',
        data: $("#form_contact").serialize(),
        success: function(data) {
        }
      });
      this.success_msg = ""
      this.success_msg = "Email successfully sent!"
      this.email_sent = true
      this.email = ''
      this.subject =''
      this.name = ''
      this.contact = ''
      this.message = ''
    },
    moveDivisor() {
      $(".banner-front").css("width", $("#slider").val()+"%");
    },
    wait(ms) {
      var start = new Date().getTime();
      var end = start;
      while(end < start + ms) {
        end = new Date().getTime();
      }
    },
    typeName: function(isName = true) {
      console.log({isName})
      //  TYPE NAME
      if (isName && this.step === 0 && this.txtIndex < this.titleName.length) {
        this.txtDisplayed += this.titleName.charAt(this.txtIndex);
        $('.identifier').html(this.txtDisplayed);
        this.txtIndex++;

        if (this.txtDisplayed.length === this.titleName.length) {
          this.step = 1;
          setTimeout(this.typeName.bind(null), 2000);
        } else {
          setTimeout(this.typeName.bind(null), 100);
        }
      }
      
      // BACKSPACE
      else if (this.step === 1 && this.txtIndex !== 0) {
        this.txtDisplayed = this.txtDisplayed.slice(0, -1);
        $('.identifier').html(this.txtDisplayed);
        this.txtIndex--;

        if (this.txtIndex === 0) {
          this.step = 0;

          setTimeout(this.typeName.bind(null, !isName), 100);
        } else {
          setTimeout(this.typeName.bind(null, isName), 100);
        }

        
      }

      // TYPE DESIGNATION
      else if (!isName && this.step === 0 && this.txtIndex < this.titleDesignation.length) {
        this.txtDisplayed += this.titleDesignation.charAt(this.txtIndex);
        $('.identifier').html(this.txtDisplayed);
        this.txtIndex++;


        if (this.txtIndex === this.titleDesignation.length) {
          this.step = 1;
          setTimeout(this.typeName.bind(null, false), 2000);
        } else {
          setTimeout(this.typeName.bind(null, false), 100);
        }
      }

      // else {
      //   setTimeout(this.typeName.bind(null, false), 100);
      // }


      console.log({
          step: this.step,
          index: this.txtIndex,
          titleName: this.titleName.length,
          displayed: this.txtDisplayed,
          displayedLength: this.txtDisplayed.length
        })

    },
    typeDesignation: function() {
      // TYPE DESIGNATION
      if (this.txtIndex < this.titleDesignation.length) {
        this.txtDisplayed += this.titleDesignation.charAt(this.txtIndex);
        $('.identifier').html(this.txtDisplayed);
        this.txtIndex++;

        setTimeout(this.typeDesignation, 100);
        if (this.txtIndex === this.titleDesignation.length) {
          this.step = 1;
        }
      }
    },
    typeWriter() {
      this.typeName();
      // this.typeDesignation();
    }
  },
  created: function() {
    this.typeWriter();
  }
}
</script>
<style src="@/assets/css/style.css" /></style>



