<template>
  <div>
    <div id="body_cont" class="" v-if="loading == false">
      <div class="col-sm-12">
        <h1 class="page-title"><span>&#60;Featured Works/&#62;</span></h1>
      </div>

      <div v-for="(project, index) in projects" class="row projects_cont">
        <div
          class="col-sm-6 img-container cpointer"
          :class="{ preview: index % 2 > 0 }"
          v-on:click="show_ss(project.id, project.type)"
        >
          <img
            :src="require(`../assets/uploads/${project.banner}`)"
            alt=""
            class="card-img-top margin-left-1000"
          />
        </div>

        <div
          class="col-sm-6 margin-right-1000"
          :class="{ 'pull-left': index % 2 > 0 }"
        >
          <div>
            <div class="center_cont">
              <div class="col-sm-12">
                <h5 class="">{{ project.title }}</h5>
                <small class="">
                  <strong> Users: {{ project.user_count }} </strong>
                </small>
                <br />
                <span>{{ project.description }}</span>
              </div>
              <div class="col-sm-12">
                <span
                  class="badge badge-dark"
                  v-for="tech in splitString(project.technology)"
                  >{{ tech }}</span
                >
              </div>
              <div class="col-sm-12">
                <a :href="project.link" target="_blank">
                  <button
                    v-if="project.type === 3"
                    class="btn btn-info btn-sm"
                  >Preview</button>
                </a>

                <button
                  v-if="project.type !== 3"
                  class="btn btn-info btn-sm"
                  v-on:click="show_ss(project.id, project.type)"
                >
                  View Screenshots
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="mobile_checker text-white"></div>

      <!-- Modal -->
      <div>
        <!-- Modal Component -->
        <b-modal size="lg" id="modal1" hide-footer title="" ref="modal_ss">
          <div class="loading_body loading_modal" v-if="modal_loading"></div>
          <div class="d-block" v-if="modal_loading == false">
            <b-carousel
              id="screenshot_carousel"
              style="text-shadow: 1px 1px 2px #333;"
              controls
              indicators
              background="#ababab"
              :interval="4000"
              v-model="slide"
              @sliding-start="onSlideStart"
              @sliding-end="onSlideEnd"
            >
              <!-- Text slides with image -->
              <div v-for="img in screenshot">
                <b-carousel-slide
                  caption=""
                  text=""
                  :img-src="require(`../assets/uploads/${img.path}`)"
                ></b-carousel-slide>
              </div>
            </b-carousel>
          </div>
        </b-modal>
      </div>
    </div>
    <div class="loading_body" v-if="loading"></div>
  </div>
</template>

<style src="@/assets/css/portfolio.css" scoped />

<script>
// export const baseUrl = process.env.PORTFOLIO;
export const baseUrl = "@/assets/json/";
import Vue from "vue";
import JQuery from "jquery";
let $ = JQuery;

import axios from "axios";
import VueAxios from "vue-axios";

import vBModal from "bootstrap-vue/es/directives/modal/modal";
import { Modal } from "bootstrap-vue/es/components";

import bCarousel from "bootstrap-vue/es/components/carousel/carousel";
import bCarouselSlide from "bootstrap-vue/es/components/carousel/carousel-slide";

import projectsJson from "@/assets/json/projects";
import screenshotsJson from "@/assets/json/screenshots";

Vue.component("b-carousel", bCarousel);
Vue.component("b-carousel-slide", bCarouselSlide);

Vue.use(Modal);
Vue.directive("b-modal", vBModal);

Vue.use(VueAxios, axios);

var is_mobile = false;

$("document").ready(() => {
  if (is_mobile == true) {
    $(".hidden_container").attr("style", "height:300px");
    $(".card-img").attr("style", "height:300px");
  }
});

$(window).on("scroll", () => {
  var scrollPosTop = $(window).scrollTop();
  $(".img-container").each((i, obj) => {
    var bottomPos =
      obj.getBoundingClientRect().bottom + $(window)["scrollTop"]();
    var newMargin = bottomPos - (scrollPosTop + $(window).height());

    if (newMargin < 100) {
      $(obj)
        .find("img")
        .removeClass("margin-left-1000")
        .addClass("margin-left-0");
      $(obj)
        .siblings("div")
        .removeClass("opacity-0")
        .addClass("opacity-1");
    } else {
      $(obj)
        .find("img")
        .removeClass("margin-left-0")
        .addClass("margin-left-1000");
      $(obj)
        .siblings("div")
        .removeClass("opacity-1")
        .addClass("opacity-0");
    }
  });
});

export default {
  data() {
    return {
      cardHeight: {},
      imageHeight: null,
      imageHeightCont: null,
      projects: projectsJson.sort((a, b) => {
          const x = a['order']; const y = b['order'];
          return ((x < y) ? -1 : ((x > y) ? 1 : 0));
        }),
      screenshot: [],
      slide: 0,
      sliding: null,
      loading: false,
      modal_loading: true
    };
  },
  mounted() {
    this.getList();
  },
  created() {
    // $(".modal-dialog").addClass("modal-full");
    // $(".modal-dialog").css({
    //   width: "100% !important",
    //   "max-width": "100% !important",
    //   height: "100% !important",
    //   "max-height": "100% !important",
    //   position: "absolute !important",
    //   top: "0 !important",
    //   bottom: "0 !important",
    //   margin: "0 !important"
    // });
  },
  methods: {
    setHeight(event) {
      let image = event.target;
      if (!is_mobile) {
        this.imageHeight = image.clientWidth * 0.5;
      } else {
        this.imageHeight = 350;
      }
    },
    getList() {
      this.check_if_mobile();
      this.click_button_next();
    },
    splitString(textToSplit) {
      return textToSplit.split(",");
    },
    async show_ss(id, type) {
      this.modal_loading = true;
      this.screenshot = await screenshotsJson.filter(row => row.portfolio_id === id);

      if (type == 0) {
        $(".modal-dialog")
          .addClass("modal-full modal-lg")
          .removeClass("modal-md");
      } else {
        $(".modal-dialog")
          .removeClass("modal-full modal-lg")
          .addClass("modal-md");
      }

      // $(".modal-dialog").css({
      //   width: "100% !important",
      //   "max-width": "100% !important",
      //   height: "100% !important",
      //   "max-height": "100% !important",
      //   position: "absolute !important",
      //   top: "0 !important",
      //   bottom: "0 !important",
      //   margin: "0 !important"
      // });
      await this.show_ss_modal();
      this.modal_loading = await false;
    },
    splitString(textToSplit) {
      return textToSplit.split(",");
    },
    show_ss_modal() {
      this.$refs.modal_ss.show();
    },
    hide_modal() {
      this.$refs.modal_ss.hide();
    },
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
    check_if_mobile() {
      if (window.matchMedia("(max-width: 991px)").matches) {
        is_mobile = true;
      }
    },
    click_button_next() {
      $(".button_next").click();
    },
    sortByKey(array, key) {
      array.sort((a, b) => {
        const x = a[key]; const y = b[key];
        // eslint-disable-next-line no-nested-ternary
        return ((x < y) ? -1 : ((x > y) ? 1 : 0));
      })
    } 
  }
};
</script>



